import axios from "axios";
import { getUserDictionary } from "../redux/ActionCreators";
import { filter, forEach, map, omit } from 'lodash'

//--------------   
export const getUserDictAPI  = () => {
    return function(dispatch) {
        const authorDictAPI = axios.get('http://127.0.0.1:8000/api/author/');
        authorDictAPI.then(respAuthor => {  //   (  )   pk 
            const userDictAPI = axios.get('http://127.0.0.1:8000/api/users/');
            userDictAPI.then(respUser =>{  //   . 
                const securedUSerDict = []
                forEach(respUser.data, function(value)  {
                    let securedValue = omit(value, ['password', 'is_superuser', 'is_staff', 'groups', 'user_permissions'])
                    securedUSerDict.push(securedValue) //       ( )
                })
                const users = [];
                securedUSerDict.map(user => {     //   
                    const ava = filter(respAuthor.data, {'name':user.id})  //        
                        if (ava.length){
                            users.push(
                                {
                                    ...user,
                                    ...{
                                        avatar: ava[0].avatar, 
                                        phone: ava[0].phone, 
                                        socialAcc: ava[0].socialAcc,
                                        author: ava[0].name, 
                                        userID: ava[0].id,
                                        isEmailConfirmed: ava[0].isEmailConfirmed,
                                        isPhoneConformed: ava[0].isPhoneConformed,
                                    }
                                }
                            )  //         
                        }
                        else {
                            users.push({...user,...{avatar: null, phone: null, author: null, userID: ava[0]?.id}}) //    -    .
                        }
                    }
                )
                dispatch(getUserDictionary(users))   //  
            })
        })
    }
}


